@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #181a19;
  --secondary-color: #116466;
  --accent-color: #d9b08c;
  --text-primary-color: #d1e8e2;
  --text-accent-color: #ffcb9a;
}

.container {
    background-color: #181a19;
    background-color: var(--primary-color);
    position: relative;
    z-index: 0;
    min-height: 100vh;
}




.footer {
    background-color: var(--primary-color);
    color: var(--text-primary-color);
    border-top: 1px solid var(--secondary-color);
    height: 60px;
    display: flex;
    justify-content: center;
  }

  .footer-menu {
      margin: 0;
      align-self: center;
      list-style: none;
      padding: 10px 80px;
  }

  .footer-menu-rights a {
      text-decoration: none;
      color: var(--text-primary-color);
      transition: 0.5s;
  }

  .footer-menu-rights a:hover {
      color: var(--text-accent-color);
  }

  @media only screen and (max-width: 675px) {
    .footer-menu-rights {
        padding-bottom: 0;
    }
  }

  @media only screen and (max-width: 470px) {

    .footer-menu {
        padding: 10px 20px;
    }
  }
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #181a19;
  --secondary-color: #116466;
  --accent-color: #d9b08c;
  --text-primary-color: #d1e8e2;
  --text-accent-color: #ffcb9a;
}

.header {
    background-color: #181a19;
    background-color: var(--primary-color);
    color: #d1e8e2;
    color: var(--text-primary-color);
    height: 60px;
    width: 100%;
    border-bottom: 1px solid #116466;
    border-bottom: 1px solid var(--secondary-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 80px;
    
    position: fixed;
    opacity: 90%;
    z-index: 2;
  }
  
  .menu {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .menu-item {
    padding-left: 10px;
  }

  .menu-item-link {
    padding-left: 10px;
    text-decoration: none;
    color: #d1e8e2;
    color: var(--text-primary-color);
    text-shadow: 2px 2px 5px #85664e;
    text-transform: uppercase;
    transition: 0.5s;
  }

  .menu-item-link:hover {
    color: #ffcb9a;
    color: var(--text-accent-color);
  }

  .logo-link{
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .logo-text {
    margin-left: 10px;
    color: #f7cbae;
    text-shadow: 2px 2px 5px #85664e;
    font-weight:900;
    font-family: 'Major Mono Display', monospace;
  }

  .logo-link:hover {
    color: #ffcb9a;
    color: var(--text-accent-color);
  }

  .nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav-btn {
    padding: 5px;
    font-size: 24px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: #d1e8e2;
    color: var(--text-primary-color);
    visibility: hidden;
  }

  @media only screen and (max-width: 676px) {
    .nav-btn {
      visibility: visible;
      position: absolute;
      top: 10px;
      right: 80px;
    }

    .nav-bar {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #116466;
      background-color: var(--secondary-color);
      transition: 1s;
      -webkit-transform: translateY(-100vh);
              transform: translateY(-100vh);
    }

    li {
      padding-bottom: 20px;
    }

    .responsive-nav {
      -webkit-transform: none;
              transform: none;
    }

    .nav-close-btn {
      position: absolute;
      top: 15px;
      right: 15px;
    }
  }

  @media only screen and (max-width: 457px) {
    .nav-btn {
      visibility: visible;
      position: absolute;
      top: 10px;
      right: 20px;
    }

    .logo {
      position: absolute;
      top: 10px;
      left: 20px;
    }

  }

  @media only screen and (max-width: 310px) {
    .logo-text {
      visibility: hidden;
    }

  }
