    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .launch-header {
    color: #116466;
    color: var(--secondary-color);
    margin-bottom: 15px;
  }

  .launch-time {
    font-size: 30px;
    margin-bottom: 20px
  }

  .countdown-container {
    border: 1px solid #d9b08c;
    border: 1px solid var(--accent-color);
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .launch-countdown-time {
    font-size: 36px;
    color: #d1e8e2;
    color: var(--text-primary-color);
  }

  .launch-countdown-label {
    color: #116466;
    color: var(--secondary-color)
  }


     .content-news-article-container {
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: var(--secondary-color);
          border-radius: 10px;
          padding: 10px 20px;
          width: 100%;
      }

      .content-news-article-image img {
        border-radius: 7px;
        box-shadow: 0 0 20px 1px var(--primary-color);
        width: 100%;
        max-height: auto;
      }

      .content-news-article-title {
        color: var(--text-accent-color);
        padding: 10px 20px;
        text-shadow: 2px 2px 8px var(--primary-color);
      }

      .content-news-article-text {
        color: var(--text-primary-color);
      }

      .content-news-article-link {
          color: var(--text-accent-color);
          transition: 0.5s;
      }

      .content-news-article-link:hover {
        color: var(--primary-color);
      }




      ul {
          list-style: none;
          display: flex;
          flex-wrap: wrap;
      }

      .content-article-list-li {
          flex: 1 1;
          flex-basis: content;
          padding: 20px 0px;
          width: 100%;
      }

      .loading {
          color: var(--text-primary-color);
      }



  .content {
    padding: 100px 80px;
