.gallery-card {
    background-color: var(--primary-color);
    border: 1px solid var(--secondary-color);
    border-radius: 10px;
    padding: 10px;
    width: fit-content;
    margin: 10px auto;
}

.gallery-card:hover {
    cursor: pointer;
}

.gallery-card-photo {
    width: 200px;
    height: auto;
    border-radius: 10px;
}